import React from "react";
import { Link, graphql } from "gatsby";

import Bio from "../components/bio";
import Layout from "../components/layout";
import Post from "../components/post";
import SEO from "../components/seo";

import "../scss/index.scss";

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const allPosts = data.allMarkdownRemark.edges;
  const numPosts = data.site.siteMetadata.settings.postsPerOnePage;
  const latestPosts = allPosts.slice(0, numPosts);

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="All posts" />
      <Bio />
      {latestPosts.map(({ node }) => <Post {...node} key={node.fields.slug} />)}
      <nav>
        <ul
          style={{
            listStyle: `none`,
            padding: 0,
            textAlign: "right"
          }}
        >
          <li>
            {allPosts.length > numPosts && (
              <Link to="page2" rel="next">
                Old Posts â†’
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  );
};

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        settings {
          postsPerOnePage
        }
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MM/DD YYYY")
            title
            description
          }
        }
      }
    }
  }
`;
